<template>
	<div id="changePwd">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">修改密码</div>
      </div>
    </div>
    <div class="login_box">
      <div class="login_bar background_color_ffffff">
        <img src="static/img/icon_mima12.png" class="img_top_center"/>
        <input class="font_14 login_input" type="password" placeholder="请输入旧密码" v-model="oldPwd"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <img src="static/img/icon_mima12.png" class="img_top_center"/>
        <input class="font_14 login_input" type="password" placeholder="请输入新密码" v-model="newPwd"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <img src="static/img/icon_mima12.png" class="img_top_center"/>
        <input class="font_14 login_input" type="password" placeholder="请再次输入新密码" v-model="newPwd2"/>
      </div>
      <div style="margin-top: 30px;" class="text_center">
        <button class="login_button" @click="submitChangePwd">确定修改</button>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'forgetPwd',
		data() {
			return {
        oldPwd:'',
        newPwd:'',
        newPwd2:'',
        userId:localStorage.getItem('userId')
			}
		},
		methods:{
      submitChangePwd(){
        if(!this.oldPwd&&!this.newPwd&&!this.newPwd2){
          this.$toast({
            message: '请将页面内容填写完整!',
            position: "bottom"
          });
          return;
        }
        if(this.newPwd!=this.newPwd2){
          this.$toast({
            message: '请确认两次密码输入相同!',
            position: "bottom"
          });
          return;
        }
        let $this = this;
        let obj = {
          userid: this.userId,
          oldPwd: this.oldPwd,
          newPwd: this.newPwd,
        };
        let qs = require('qs');
        $this.$ajax.post("user/updatePwd", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '密码修改成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      }
		}
	}
</script>
<style scoped>
  .login_button {
    width: 80%;
    height: 40px;
    line-height: 40px;
    border: 0;
    color: #ffffff;
    border-radius: 5px;
    background-color: #c10804;
  }
  .send_code{
    padding: 2px 5px;
    border: 1px solid #c10804;
    border-radius: 15px;
    color: #c10804;
  }
  .login_span{
    position: relative;
    left: 43%;
    bottom: 32px;
    color: #ffffff;
  }
  .login_btn{
    width: 96%;
    margin-left: 2%;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .width_5{
    width: 5%;
  }
  .login_box{
    margin-top: 44px;
  }
  .login_top{
    border-top: 5px solid #eeeeee;
  }
  .login_bar{
    padding: 15px 15px;
  }
</style>



